<template>
  <el-table
    :data="list"
    border
    style="width: 100%">
    <el-table-column
      fixed
      type="index"
      label="id"
    >
    </el-table-column>
    <el-table-column
      prop="node"
      label=""
      show-overflow-tooltip
    >
    </el-table-column>
    <el-table-column
      prop="remarks"
      label=""
      width="100"
      show-overflow-tooltip
    >
    </el-table-column>
    <el-table-column
      label=""
      width="100">
      <template slot-scope="scope">
        <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
import { DelSubNode } from '@/api/sub'
export default {
  name: 'NodeList',
  data () {
    return {
    }
  },
  props: {
    list: Array
  },
  created () {
    console.log(this.list)
  },
  methods: {
    handleClick ({ id }) {
      console.log(id)
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        center: true
      }).then(async () => {
        const { code, msg } = await DelSubNode(id)
        if (code === 200) {
          this.$emit('DelSubNode', id)
        }
        this.$message({
          type: code === 200 ? 'success' : 'warning',
          message: msg
        })
      }).catch(() => {

      })
    }

  }
}
</script>
